import SkillView from "@/views/SkillView.vue";

export default defineComponent({
  components: {
    HomeView,
    AboutView,
    Waypoint,
    EnEView,
    SkillView,
  },
  data() {
    return {
      isTop: true,
      navLists: [
        {
          name: "Home",
          path: "#home",
        },
        {
          name: "About",
          path: "#about",
        },
        {
          name: "Experience & Education",
          path: "#experienceAndEdu",
        },
        {
          name: "Skill",
          path: "#skill",
        },
        {
          name: "Project",
          path: "#project",
        },
        {
          name: "Download My CV",
          path: "#cv",
        },
      ],
    };
  },
  methods: {
    onchange(state: any) {
      console.log(state.going);
      if (state.going == "OUT") {
        this.isTop = false;
      } else {
        this.isTop = true;
      }
